import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import data from '../../data.json';
import './gallery.css';

class Gallery extends Component {
  getImages() {
    const images = data.myProject[this.props.project].gallery.map(item => {
      return {
        original: process.env.PUBLIC_URL + '/media/' + this.props.project +'/' + item + '?raw=true',
        thumbnail: process.env.PUBLIC_URL + '/media/'+ this.props.project +'/thumbnail/' + item + '?raw=true'
      };
    });
    return images;
  }

  render() {
    const images = this.getImages();
    return <ImageGallery items={images} />;
  }
}

export default Gallery;
