import React, { Component } from 'react';
import './education.scss';

class Education extends Component {
  render() {
    return (
    <div className='education-me-wrapper'>
        <div className="sections">
            <h1 className='titleClass'>Образование<i className="linked-icon-title">&#xf19d;</i></h1>
            <div className='section-description'>
                <div className='section-description'>
                    <div className='text-part'>
                        <span  className='font-header'>Тихоокеанский государственный университет 2007 - 2014</span>
                        <span><b>Факультет архитектуры и строительства</b></span>
                        <span><b>Специальность:</b> Архитектура </span>
                        <span id='experience'><b>Направление:</b> Магистр архитектуры </span>
                    </div>                             
                </div>
         
            </div>
        </div>
    </div>        
    );
  }
}

export default Education;


