import React, { Component } from 'react';
import Gallery from '../gallery/gallery'
//import './project.scss';
import data from '../../data.json';



class Project extends Component {
  render() {
    return (
        <div className='education-me-wrapper'>
    <div className="sections">
        <div className='section-description-prg'>
            <div className='section-description-prg'>
                <div className='text-project-description'>{data.myProject[this.props.nameProject].info}</div>
                <Gallery project = {this.props.nameProject}></Gallery>
            </div>
        </div>
    </div>
    </div>        
    );
  }
}

export default Project;


