import React, { Component } from 'react';
import './App.scss';
import Header from './components/header/header';
import Aboutme from './components/aboutme/aboutme';
import Education from './components/education/education';
import Footer from './components/footer/footer';
import Project from './components/project/project';
import BiographyTimeLine from './components/timeline/biography-time-line'

class App extends Component {
  render() {
    return (
      <div>
          <Header></Header>
          <Aboutme></Aboutme> 
          <div className='education-me-wrapper'>
            <div className="sections" >
              <h1 className='titleClass'>Проекты<i className="linked-icon-title">&#xe806;</i></h1>
            </div>
          </div>  
          <Project nameProject='project01'></Project>
          <Project nameProject='project02'></Project>
          <Project nameProject='project03'></Project>
          <Project nameProject='project04'></Project>
          <Project nameProject='project05'></Project>
          <Project nameProject='project06'></Project>
          <Project nameProject='project07'></Project>
          <div className='education-me-wrapper'>
            <div className="sections" id='english'>
              <h1 className='titleClass'>Навыки<i className="linked-icon-title">&#xf1fc;</i></h1>
              <div className='section-description'>
                <div className='section-description'>
                    <div className='text-part'>                        
                        <span>Владение ПК</span>
                        <span id='education'>Знание программ: текстовых и графических редакторов <b>(ArchiCad, AutoCad (SPDS), Autodesk Revit, Photoshop, 3D Max, SketchUp, CorelDRAW)</b></span>
                    </div>                             
                </div>         
            </div>
            </div>
          </div>  
          <Education></Education> 
          <div className='education-me-wrapper'>
            <div className="sections" >
              <h1 className='titleClass'>Опыт работы<i className="linked-icon-title">&#xe805;</i></h1>
            </div>
          </div>        
          <BiographyTimeLine></BiographyTimeLine>
          <Footer></Footer>
      </div>
    );
  }
}

export default App;